<template>
  <el-form
    :model="ruleForm"
    status-icon
    :rules="rules"
    ref="ruleForm"
    class="demo-ruleForm aa"
  >
    <el-form-item prop="userName">
      <el-input
        type="text"
        v-model="ruleForm.userName"
        autocomplete="off"
        label-width="20px"
      ></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input
        type="password"
        v-model="ruleForm.checkPass"
        autocomplete="off"
      ></el-input>
    </el-form-item>
    <el-checkbox v-model="checked" :checked="checked">记住密码</el-checkbox>
    <el-form-item>
      <el-button type="primary" class="submit" @click="submitForm('ruleForm')"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "LoginCRM",
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        userName: "",
        checkPass: "",
      },
      checked: false,
      rules: {
        userName: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
      },
    };
  },
  methods: {
    submitForm(formName) {
      if (this.checked) {
        localStorage.setItem("ruleForm", JSON.stringify(this.ruleForm));
        localStorage.setItem("checked", this.checked);
        sessionStorage.setItem("userName", this.ruleForm.userName);
        sessionStorage.setItem("checkPass", this.ruleForm.checkPass);
      } else {
        localStorage.removeItem("ruleForm");
        localStorage.removeItem("checked");
        sessionStorage.setItem("userName", this.ruleForm.userName);
        sessionStorage.setItem("checkPass", this.ruleForm.checkPass);
      }

      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$message({
            message: `欢迎登录CRM管理系统,${this.ruleForm.userName}`,
            type: "success",
          });
          this.$router.push({
            path: "/crm",
            query: {
              userName: this.ruleForm.userName,
              checkPass: this.ruleForm.checkPass,
            },
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  beforeMount() {
    if (
      localStorage.getItem("checked") &&
      JSON.parse(localStorage.getItem("ruleForm"))
    ) {
      this.checked = !!localStorage.getItem("checked");
      this.ruleForm = JSON.parse(localStorage.getItem("ruleForm"));
    }
  },
};
</script>

<style scoped>
.el-checkbox {
  margin-top: 20px;
  float: left;
}
.submit {
  width: 100%;
  margin-top: 20px;
}
</style>