import Vue from 'vue'
import VueRouter from 'vue-router'
import LoginView from '../views/LoginView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'login',
    component: LoginView
  },
  {
    path: '/',
    name: 'login',
    component: LoginView
  },
  {
    path: '/crm',
    name: 'CRM',
    component: () => import('../views/CRMView.vue'),
    children: [
      {
        path: 'user',
        name: 'user',
        component: () => import('../views/UserView.vue')
      },
      {
        path: 'teacher',
        name: 'teacher',
        component: () => import('../views/TeacherView.vue')
      },
      {
        path: 'role',
        name: 'role',
        component: () => import('../views/RoleView.vue')
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.path === '/') {
    next()
  } else {
    if (sessionStorage.getItem("userName") === 'admin' && sessionStorage.getItem("checkPass") === '123') {
      next();
    } else {
      alert('用户名或密码错误')
      next('/')
    }
  }
})

export default router
